<template>
    <div class="orderList" :class={themel:bindThemeL}>
        <!-- 头部 -->
        <navigation-bar  @onLeftClick="onLeftClick" pageName="我的订单" ref="navigation">
            <template v-slot:nav-right>
                <div style="width:100%;height: 100%;display: flex;align-items: center;justify-content: center;"
                     @click="goToOrderSearch">
                    <img style="width: 22px;height: 22px;" src="@/assets/images/newSlicing/searchbar_icon_search_normal.png" alt="">
                </div>
            </template>
        </navigation-bar>
        <van-tabs v-model="active" :swipeable="swipeable" animated @click="tabClick" @change="tabChange">
            <van-tab ref="vantTab"  title="全部" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
                <div class="scrollY" ref="scrollContent" v-if="active == 0">
                    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                @load="onLoad"
                                v-if="lists.length > 0"
                                :offset="30"
                                :immediate-check="immediateBol"
                        >
                            <div class="topSpace"></div>
                            <order-item @refresh="onRefresh" @setSwipeable="setSwipeable" @setExpressInfo="setExpressInfo" @setSelectOrderItem="setSelectOrderItem" @childPayFun="goToPay" :lists="lists"/>
                            <div class="topSpace"></div>
                        </van-list>
                        <div class="noList" v-if="lists.length == 0&&!loading">
                            <img src="@/assets/images/default/no_list_img.png" />
                            <div>暂无订单</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
            <van-tab  title="待付款" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
                <div class="scrollY" ref="scrollContent" v-if="active == 1">
                    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                @load="onLoad"
                                v-if="lists.length > 0"
                                :offset="30"
                                :immediate-check="immediateBol"
                        >
                            <div class="topSpace"></div>
                            <order-item @refresh="onRefresh" @setSwipeable="setSwipeable" @setSelectOrderItem="setSelectOrderItem" @childPayFun="goToPay" :lists="lists"/>
                            <div class="topSpace"></div>
                        </van-list>
                        <div class="noList" v-if="lists.length == 0&&!loading">
                            <img src="@/assets/images/default/no_list_img.png" />
                            <div>暂无订单</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
            <van-tab  title="待发货" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
                <div class="scrollY" ref="scrollContent" v-if="active == 2">
                    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                        <!--                    v-if="list.length > 0"-->
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                @load="onLoad"
                                v-if="lists.length > 0"
                                :offset="30"
                                :immediate-check="immediateBol"
                        >
                            <div class="topSpace"></div>
                            <order-item @refresh="onRefresh" @setSwipeable="setSwipeable" @setSelectOrderItem="setSelectOrderItem" :lists="lists"/>
                            <div class="topSpace"></div>
                        </van-list>
                        <div class="noList" v-if="lists.length == 0&&!loading">
                            <img src="@/assets/images/default/no_list_img.png" />
                            <div>暂无订单</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
            <van-tab  title="待收货" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
                <div class="scrollY" ref="scrollContent" v-if="active == 3">
                    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                        <!--                    v-if="list.length > 0"-->
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                @load="onLoad"
                                v-if="lists.length > 0"
                                :offset="30"
                                :immediate-check="immediateBol"
                        >
                            <div class="topSpace"></div>
                            <order-item @refresh="onRefresh" @setSwipeable="setSwipeable" @setExpressInfo="setExpressInfo" @setSelectOrderItem="setSelectOrderItem" :lists="lists"/>
                            <div class="topSpace"></div>
                        </van-list>
                        <div class="noList" v-if="lists.length == 0&&!loading">
                            <img src="@/assets/images/default/no_list_img.png" />
                            <div>暂无订单</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
            <van-tab  title="待评价" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
                <div class="scrollY" ref="scrollContent" v-if="active == 4">
                    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                        <!--                    v-if="list.length > 0"-->
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                @load="onLoad"
                                v-if="lists.length > 0"
                                :offset="30"
                                :immediate-check="immediateBol"
                        >
                            <div class="topSpace"></div>
                            <order-item @refresh="onRefresh" @setSwipeable="setSwipeable" @setSelectOrderItem="setSelectOrderItem" :lists="lists"/>
                            <div class="topSpace"></div>
                        </van-list>
                        <div class="noList" v-if="lists.length == 0&&!loading">
                            <img src="@/assets/images/default/no_list_img.png" />
                            <div>暂无订单</div>
                        </div>
                        <div style="height: 300px;" v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
            <van-tab  title="已完成" color="#17B7BD" line-width="0.67rem" line-height="0.08rem" :title-style="{fontSize: titleThemeL}">
                <div class="scrollY" ref="scrollContent" v-if="active == 5">
                    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                        <!--                    v-if="list.length > 0"-->
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                @load="onLoad"
                                v-if="lists.length > 0"
                                :offset="30"
                                :immediate-check="immediateBol"
                        >
                            <div class="topSpace"></div>
                            <order-item @refresh="onRefresh" @setSwipeable="setSwipeable" @setSelectOrderItem="setSelectOrderItem" :lists="lists"/>
                            <div class="topSpace"></div>
                        </van-list>
                        <div class="noList" v-if="lists.length == 0&&!loading">
                            <img src="@/assets/images/default/no_list_img.png" />
                            <div>暂无订单</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
        </van-tabs>

        <!-- 去支付时显示微信或者支付宝支付 -->
        <van-popup v-model="payWindowShow" position="bottom" class="vanPopup" @close="closePopup">
            <div class="payModeBox">
                <div class="payModeTitle">支付方式</div>
                <div v-show="isAcppay" class="payModeItem" @click="alipay(2)">
                    <div class="unipayclass">
                        <img class="unipayIcon" src="@/assets/images/common/icon_unipay.png" alt="">
                        <div style="display: inline-block; vertical-align:middle;">云闪付</div>
                        <img class="iconUnipayRight" src="@/assets/images/common/icon_unipay_right.png" alt="" style="display: inline-block;vertical-align:middle;width: 21px;height: 21px;">
                        <div class="saleIcon" style="display: inline-block;vertical-align:middle;" v-if="isAcpPayTag">{{acpPayTag}}</div>
                    </div>
                </div>
                <div v-show="isAlipay" class="payModeItem" @click="alipay(1)">
                    <img src="@/assets/images/common/icon_alipay.png" alt="">
                    <div>支付宝</div>
                </div>
<!--                <div v-show="isWechart" class="payModeItem" @click="alipay(0)">-->
<!--                    <img src="@/assets/images/common/icon_wechart.png" alt="">-->
<!--                    <div>微信</div>-->
<!--                </div>-->
            </div>
            <div class="cancelBtn" @click="closePopup">取消</div>
        </van-popup>
        <!--打电话弹窗-->
        <van-popup v-model="isShowPhoneSelect" position="bottom" class="vanPopup" @close="onCancel">
            <div class="phoneSelectBox">
                <div v-if="selectOrderItem" class="phoneSelect" @click="callPhone(selectOrderItem.shop.shopPhone)">
                    <div>商家电话：</div>
                    <div class="phoneNum">{{selectOrderItem.shop.shopPhone}}</div>
                </div>
                <div v-if="selectOrderItem" class="phoneSelect" @click="callPhone(selectOrderItem.serviceTel)">
                    <div>平台客服：</div>
                    <div class="phoneNum">{{selectOrderItem.serviceTel}}</div>
                </div>
            </div>
            <div class="cancelPhoneBtn" @click="onCancel">取消</div>
        </van-popup>

        <van-action-sheet v-model="isShowAction" :title="isShowTitle==0?'商家配送说明':'物流跟踪'">
            <div class="actionContent" v-if="isShowTitle==1">
                <div>物流单号：{{expressInfo.expressTrackNo}}
                    <img style="width: 13px;height: 13px;margin-left: 5px" src="../../assets/images/newSlicing/copy_cut.png" alt=""
                         @click.stop="copyBtn(expressInfo.expressTrackNo)"></div>
                <div>物流公司：{{expressInfo.expressCompany}}</div>
                <div  @click.stop="callProperty(expressInfo.shopPhone)">商家电话：<span style="color: #4A90E2;">{{expressInfo.shopPhone}}</span></div>
            </div>
            <div class="actionContent" v-if="isShowTitle==0">
                <div>{{expressInfo.accountFor}}</div>
            </div>
        </van-action-sheet>
        <serviceItem :serviceInfo="serviceInfo" @setServiceInfo="setServiceInfo"></serviceItem>

    </div>
</template>

<script>
    import {mapActions, mapState} from "vuex";
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import orderItem from '@/page/superiorProduct/orderItem'
    import NetworkError from '@/common/networkError/NetworkError'
    import { prepareToPayOrderService, orderDetailService, unionOrderPrepayV34Service } from '../../services/productOrderService.js';
    import { Toast, Dialog,ActionSheet  } from "vant";
    import { storage } from '../../utils/storage';
    import serviceItem from "../superiorProduct/serviceItem"

    export default {
        name:"orderList",
        components:{
            NavigationBar,
            orderItem,
            serviceItem,
            NetworkError
        },
        data(){
            return{
                isShowAction:false,
                isShowTitle:-1,
                selectOrderItem:null,
                isShowPhoneSelect:false,
                immediateBol:false,
                swipeable:true,
                lists:[],
                goodsCount:0,
                pageFlag:0,
                loading: true,
                finished: true,
                active:0,
                isLoading: false,
                /*大字模式*/
                bindThemeL: this.$curTheme==0?false:true,
                titleThemeL: this.$curTheme==0?"14px":"17px",
                rows:10,
                tabBarList:[{
                    title:'全部',
                    reqIndex:0
                },{
                    title:'待付款',
                    reqIndex:1
                },{
                    title:'待发货',
                    reqIndex:10
                },{
                    title:'待收货',
                    reqIndex:3
                },{
                    title:'待评价',
                    reqIndex:2
                },{
                    title:'已完成',
                    reqIndex:4
                }],
                payWindowShow: false,//去支付弹窗
                isAlipay: false,//是否支付宝支付
                isWechart: false,//是否微信支付
                isAcppay:false,//云闪付支付选择
                isAcpPayTag:false,//是否有满减标签

                orderListScrollTop:0,//页面滑动高度
                returnFlag:false,
                expressInfo:null,//物流信息

                isNoNetwork:false, //是否没有网络
                serviceInfo : null,
                showServiceItem :false,
            }
        },
        mounted(){
        },
        activated(){
            this.bindThemeL= this.$curTheme==0?false:true;
            this.$nextTick(()=>{
                let bodyHeight = document.documentElement.clientHeight || document.body.clientHeight;
                this.$refs.vantTab.$el.parentNode.parentNode.style.height = (bodyHeight-44-this.$stbHeight-50)+'px'
            })
            this.returnFlag=this.$route.query.ifPayReturn;
            if (this.$route.query.flag === 10 && this.returnFlag==true) {//如果是从支付结果页面跳转回我的订单，默认显示全部订单
                this.active = 0;
                this.isShowTitle = -1;
                this.isShowAction = false;
                this.isNoNetwork = false;
                this.$refs.scrollContent.scrollTop = 0;
                this.returnFlag=false;
                this.init();
            }else if(this.$route.params.orderType==0||this.$route.params.orderType){//如果从我家首页进入订单列表
                this.active = this.$route.params.orderType;
                this.$refs.scrollContent.scrollTop = 0;
                this.isShowTitle = -1;
                this.isShowAction = false;
                this.isNoNetwork = false;
                this.init();
            }else{
                this.$refs.scrollContent.scrollTop = this.orderListScrollTop;
                this.updateData();
            }
            window.callback_pay_online=this.callback_pay_online;
            window.callBack_clipboard=this.callBack_clipboard;
        },
        deactivated(){

        },
        watch:{
            active:{
                handler(newName, oldName) {
                    this.lists = [];
                    this.isNoNetwork = false;
                    this.loading = true;
                    this.finished = false;
                    if(this.loading) {
                        this.init();
                    }
                },
            },
            /*监控到物理返回键点击状态*/
            orderListEventBackButtonFlag(val) {
                if (val) {
                    if(this.payWindowShow==true){
                        this.payWindowShow=false;
                    }else if(this.isShowPhoneSelect){
                        this.isShowPhoneSelect = false;
                    }else if(this.isShowAction){
                        this.isShowAction = false;
                    }else{
                        this.onLeftClick();
                    }
                    this.$store.dispatch('setOrderListEventBackButtonFlag', false);
                }
            }
        },
        computed: mapState({
            pageNumber: state => state.productPage.orderListPage,
            orderListEventBackButtonFlag: state => state.goBack.orderListEventBackButtonFlag

        }),
        methods:{
            ...mapActions(["getOrderListGoods","getEvaluatedGoods"]),
            //复制功能
            copyBtn(copyNum) {
                let paramsData=copyNum;
                if (this.$devicePlatform =="ios"){
                    if (window.webkit && window.webkit.messageHandlers && window.webkit.messageHandlers.JSCtrl){
                        window.webkit.messageHandlers.JSCtrl.postMessage(['request_clipboard',666,paramsData]);
                    }else {
                        window.webkit.messageHandlers.naviApp.request_clipboard({
                            'pid':666,
                            'paramsData':paramsData
                        })
                    }
                }else if (this.$devicePlatform == "android"){
                    window.store.request_clipboard(666,paramsData);
                }
            },
            callBack_clipboard(paramsData) {
                if (paramsData=='1'){
                    Toast("复制成功");
                }else {
                    console.log(paramsData);
                }
            },
            //设置物流信息
            setExpressInfo(expressInfo){
                console.log(expressInfo)
                this.expressInfo = expressInfo;
                this.isShowAction = true;
                if(expressInfo.expressCode=='other'){
                    this.isShowTitle = 1;
                }else{
                    this.isShowTitle = 0;
                }
            },
            //取消电话弹窗
            onCancel(){
                this.isShowPhoneSelect = false;
            },
            //选中的订单信息
            setSelectOrderItem(orderItem){
                // this.selectOrderItem = orderItem;
                // this.isShowPhoneSelect = true;
                this.serviceInfo = orderItem.shop.serviceInfo;
                this.showServiceItem = true;

            },
            setServiceInfo(obj){
                this.serviceInfo = obj;
            },
            // 联系商家
            callPhone(phone) {
                this.onCancel();
                if (this.$deciceType=='phone'){
                    window.location.href = "tel:" + phone;
                }
            },
            callProperty(phoneNum) {
                // window.location.href = "tel:" + this.OrderDetailData.shopList[0].shopPhone;
                const platform = navigator.userAgent;
                const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
                if (isiOS) {
                    setTimeout(
                        function(){
                            window.location.href = "tel:" + phoneNum;
                        }, 0);
                } else {
                    Dialog.confirm({
                        title: "拨打电话",
                        message: phoneNum,
                    }).then(() => {
                        if (this.$deciceType=='phone'){
                            window.location.href = "tel:" + phoneNum;
                        }
                    }).catch(() => {
                        // on cancel
                        Dialog.close;
                    });
                }
            },
            setSwipeable(swipeable){
                this.swipeable = swipeable;
            },
            //初始化
            init(){
                this.page = 1;
                this.rows = 10;
                if (this.active == 4) {
                    this.unevaluatedOrders(1);
                }else {
                    this.getAllOrders(1);
                }
            },
            updateData(){
                this.page = this.pageNumber;
                this.rows = this.lists.length;
                if (this.active == 4) {
                    this.unevaluatedOrders(1,true,false);
                }else {
                    this.getAllOrders(1,true,false);
                }
            },
            /**
             * 待评价
             * */
            unevaluatedOrders(pageNumber, isRefresh = true, isRecordPage = true){
                let paramData = {
                    page: pageNumber,
                    row: this.rows,
                    ownerCode: this.$userInfo.OWNER_CODE,
                    ownerPhone: this.$userInfo.PHONE,
                }
                this.getEvaluatedGoods(paramData).then(res => {
                    if (res.resultCode == 0) {
                        if (isRefresh) {
                            this.lists = res.data;
                            if(isRecordPage){
                                this.$store.commit("orderListPageChange", {
                                    custPageNum: true,
                                    pageNumber: 2
                                });
                            }else{
                                this.$store.commit("orderListPageChange", {
                                    custPageNum: true,
                                    pageNumber: this.page
                                });
                            }

                        } else {
                            this.lists = this.lists.concat(res.data)
                        }
                        this.lists.map((item) => {     //计算商品个数
                            item.billCate2= 2;//给待评价订单新增一个字段，便于区分订单类别
                            this.goodsCount = 0;
                            item.shop.skuList.map((skuItem) => {
                                this.goodsCount += skuItem.skuCount;
                                skuItem.picUrl=skuItem.spuPicUrl;//防止传给申请售后的图片为本地图片
                            });
                            item.goodCount = this.goodsCount;
                        });
                        this.loading = false;
                        this.isLoading = false;
                        this.isNoNetwork = false;
                        this.finished = this.lists.length>=res.total?true:false;
                    }
                }).catch(error => {
                    this.lists = [];
                    this.loading = true;
                    this.isLoading = false;
                    this.isNoNetwork = true;
                });
            },
            /**
             * 其他列表数据
             * @param
             * page   Number     页数
             * rows  Number  个数
             **/
            getAllOrders(pageNumber, isRefresh = true, isRecordPage = true) {
                let paramData = {
                    page: pageNumber,
                    row: this.rows,
                    ownerCode: this.$userInfo.OWNER_CODE,
                    billCate: this.tabBarList[this.active].reqIndex,//0代表全部类型的订单
                    ownerPhone: this.$userInfo.PHONE,
                }
                this.getOrderListGoods(paramData).then(res => {
                    console.log(res)
                    if (res.resultCode == 0) {
                        if (isRefresh) {
                            this.lists = res.data;
                            if(isRecordPage){
                                this.$store.commit("orderListPageChange", {
                                    custPageNum: true,
                                    pageNumber: 2
                                });
                            }else{
                                this.$store.commit("orderListPageChange", {
                                    custPageNum: true,
                                    pageNumber: this.page
                                });
                            }
                        } else {
                            this.lists = this.lists.concat(res.data)
                        }
                        this.lists.map((item) => {     //计算商品个数
                            this.goodsCount = 0;
                            item.shop.skuList.map((skuItem) => {
                                this.goodsCount += skuItem.skuCount;
                                skuItem.picUrl=skuItem.spuPicUrl;//防止传给申请售后的图片为本地图片
                            });
                            item.goodCount = this.goodsCount;
                            for (let i = 0;i<item.shop.skuList.length;i++){
                                let skuItem = item.shop.skuList[i];
                                if (skuItem.expressCompany !=null){
                                    if (skuItem.expressCompany.indexOf('达达') != -1 || skuItem.expressCompany.indexOf("顺丰") != -1){
                                        item.hasExpressStatus=true;
                                        item.expressCompany=skuItem.expressCompany;
                                        return;
                                    }
                                }
                            }
                        });
                        this.loading = false;
                        this.isLoading = false;
                        this.isNoNetwork = false;
                        this.finished = this.lists.length>=res.total?true:false;
                    }
                }).catch(error => {
                    this.lists = [];
                    this.loading = true;
                    this.isLoading = false;
                    this.isNoNetwork = true;
                });
            },
            //下拉刷新
            onRefresh() {
                setTimeout(() => {
                    this.page = 1;
                    this.list = [];
                    this.loading = true;
                    this.finished = false;
                    this.init()
                }, 500);
            },
            //关闭去支付弹窗
            closePopup() {
                this.payWindowShow = false;
            },
            /*银联下单支付*/
            alipay(index) {//x订单编码//y支付金额,index 0 微信 1 支付宝 p 支付参数
                let _this = this;
                let newIndex=index;
                if (index==0){
                    newIndex=1;
                }else if (index==1){
                    newIndex=0;
                }
                let pram = {
                    'merOrderId': this.payParam.merOrderId,
                    'billNo':this.payParam.billNo,
                    'payType': newIndex,
                    'channelId':storage.getStorage("channelId", false) !=undefined ? storage.getStorage("channelId", false):null
                }
                unionOrderPrepayV34Service(pram).then(res => {
                    this.closePopup(true);
                    if (res.resultCode !== "0") {
                        Dialog.alert({
                            message: res.msg
                        }).then(() => {
                        });
                        return false;
                    }
                    if (index ===2){
                        this.closePopup(true);
                        let pid = 666;
                        let param = {};
                        param.tn = res.data.appPayRequest.tn;
                        param.payMode = this.payParam.acpGatewayType;
                        let paramsData={
                            data:{
                                'payData':JSON.stringify(param),
                                'schemesUrl':''},
                            'encoder':'base64',
                            'payType':'UnionPay'
                        };
                        if (this.$devicePlatform =="ios"){
                            if (window.webkit && window.webkit.messageHandlers && window.webkit.messageHandlers.JSCtrl){
                                window.webkit.messageHandlers.JSCtrl.postMessage(['request_pay_online',pid,paramsData]);
                            }else {
                                window.webkit.messageHandlers.unifypay.request_pay_online({
                                    'pid':666,
                                    'paramsData':paramsData
                                })
                            }
                        }else if (this.$devicePlatform == "android"){
                            window.store.request_pay_online(pid,JSON.stringify(paramsData));
                        }
                    }else {
                        this.payParam.payWay = index;
                        //监听ios支付结果 检测当前订单状态
                        this.closePopup(true);
                        let pid = 666;
                        console.log(JSON.stringify(res.data.appPayRequest));
                        let paramsData={
                            data:{
                                'payData':JSON.stringify(res.data.appPayRequest),
                                'schemesUrl':''},
                            'encoder':'base64'
                        };
                        console.log(paramsData);
                        if (index==1){
                            paramsData.payType='Alipay';
                        }else if(index==0) {
                            paramsData.payType='WeChatPay';
                        }
                        if (this.$devicePlatform =="ios"){
                            if (window.webkit && window.webkit.messageHandlers && window.webkit.messageHandlers.JSCtrl){
                                window.webkit.messageHandlers.JSCtrl.postMessage(['request_pay_online',pid,paramsData]);
                            }else {
                                window.webkit.messageHandlers.unifypay.request_pay_online({
                                    'pid':666,
                                    'paramsData':paramsData
                                })
                            }
                        }else if (this.$devicePlatform == "android"){
                            window.store.request_pay_online(pid,JSON.stringify(paramsData));
                        }
                    }
                }).catch(error => {
                    this.closePopup(true);
                    Dialog.alert({
                        message: '网络异常，请稍后重试！'
                    }).then(() => {
                    });
                    console.log(error);
                });
            },
            //支付完成后回调
            payCallBack() {
                this.getOrderDetail();
            },
            callback_pay_online(pid, paramsData){
                this.getOrderDetail();
            },
            // 支付完成后获取订单详情查询订单状态
            getOrderDetail: function () {
                var data = {
                billNo: this.payParam.billNo,
                ownerCode: this.$userInfo.OWNER_CODE
                }
                console.log("getOrderPayDetail");
                orderDetailService(data).then(res => {
                if ("0" == res.resultCode) {//true表示返回成功
                    if (res.data.billStatus == "02") {

                    /* 取消监听，避免重复监听 */
                    // if (this.$devicePlatform != "pc") {
                    //     window.broadcaster.removeEventListener("orderListPayBack",this.payCallBack);
                    // }
                    this.$router.push({ name: "paySuccess", params: { orderData: res.data } });
                    console.log("支付成功");
                    } else {
                    //其他情况跳转订单列表页
                    console.log("支付失败");
                    }
                } else {
                    //其他情况跳转订单列表页
                    console.log("查询订单状态失败");
                }
                }).catch(error => {
                });
            },

            onLeftClick() {
                //1、从优品首页入口取消支付进入订单列表的情况:返回优品首页
                //2、从购物车入口取消支付进入订单列表的情况：给原生层发送信息退出h5
                if(this.$enterPage=='YouPin'){
                if (this.$route.query.flag == 10||this.$route.query.flag=='10') {
                        this.$router.push({ path: '/productPage' });
                    }
                } else {
                    //用于app处理返回
                    let backResult=this.$setRouterIndex();
                    if(backResult){
                        this.$router.go(-1);
                    }
                }
            },
            //支付
            goToPay(val) {
                this.payParam = val;
                if (1 == this.payParam.IS_ALI_PAY) {//判断是否支持微信或者支付宝支付
                    this.isAlipay = true;
                } else {
                    this.isAlipay = false;
                };
                if (1 == this.payParam.IS_WX_PAY) {
                    this.isWechart = true;
                } else {
                    this.isWechart = false;
                };
                if (1 == this.payParam.IS_ACP_PAY) {
                    this.isAcppay = true;
                } else {
                    this.isAcppay = false;
                };
                if (this.payParam.acpPayTag != "" && this.payParam.acpPayTag != undefined &&this.payParam.acpPayTag != null){
                    this.acpPayTag = this.payParam.acpPayTag;
                    this.isAcpPayTag = true;
                }
                //显示支付弹窗
                this.payWindowShow = true;
            },
            //去订单搜索页
            goToOrderSearch(){
                this.$router.push({
                    name: 'orderSearch',
                    params: {
                        billCate: this.tabBarList[this.active].reqIndex
                    }
                })
            },
            //切换tab
            tabChange(name,title){

            },
            tabClick(){

            },
            //加载更多
            onLoad(){
                this.rows = 10;
                if (this.active == 4) {
                    this.unevaluatedOrders(this.pageNumber,false);
                }else {
                    this.getAllOrders(this.pageNumber,false);
                }
            },

        },
        beforeRouteLeave(to, form, next) {
            this.orderListScrollTop = this.$refs.scrollContent.scrollTop;
            next();
        }
    }
</script>

